*{
    margin: 0;
    padding: 0;
  }
  body{
    background-color:#f0d9bd;
    font-family: sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    color: #f0d9bd;
  }
  h1,.header{
    color: indigo;
    text-align: start;
    margin-bottom: 5px;
  }
  .app{
    background-color: indigo;
    width: 95%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
    display: flex;
    justify-content: space-between;
  }
  .Question-Section{
    width: 100%;
    position: relative;
  }
  .Question-count{
    margin-bottom: 28px;
  }
  .Question-count span{
    font-size: 28px;
  }
  .Question-text{
    margin-bottom: 12px;
    font-size: 20px;
    color: #f0d9bd;
  }
  .Answer-section{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  button{
    z-index: 100;
    width: 100%;
    font-size: 16px;
    color: #fff;
    background-color: indigo;
    border-radius: 15px;
    display: flex;
    padding: 5px;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid #dfe8f1;
  }

  button:focus,button:hover{
    outline: none;
  }
  .score-card{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color:beige;
    font-size: 30px;
    font-weight: bold;
    
  }
  form button{
    z-index: 100;
    width: 100%;
    font-size: 16px;
    color: #fff;
    background-color: indigo;
    border-radius: 15px;
    display: flex;
    padding: 5px;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid #dfe8f1;
    margin-top: 15px;
  }
